.container {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    
    // @media screen and (min-width: 320px) {
       
    // }

    @include mq(mobile) {
        width: 480px;}
   
    @include mq(tablet) {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    @include mq(desktop) {
        width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
    }
}
